@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  font-family: "Barlow Semi Condensed", sans-serif;
  background: white;
  background: radial-gradient(circle, hsl(214, 47%, 23%), hsl(237, 49%, 15%));
  text-align: center;
}

/* FEUILLE */
.paper {
  height: 97px;
  width: 97px;
  background-image: url(/RPSLS/static/media/icon-paper.8b57a6b1.svg);
  background-color: white;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 40%;
  border: 10px solid hsl(230, 89%, 62%);
  border-radius: 50%;
  box-shadow: inset 0px 5px 6px grey;
}
.paper:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
@media (min-width: 768px) {
  .paper {
    background-size: 45%;
    height: 145px;
    width: 145px;
    border: 20px solid hsl(230, 89%, 62%);
  }
}
@media (max-height: 668px) {
  .paper {
    background-size: 45%;
    height: 90px;
    width: 90px;
  }
}

