.icon.paper,
.icon.scissors,
.icon.rock,
.icon.lizard,
.icon.spock {
  align-self: center;
  margin-bottom: 28px;
}

.paper.winner,
.scissors.winner,
.rock.winner,
.lizard.winner,
.spock.winner {
  box-shadow: inset 0px 8px 8px grey,
    0px 0px 0px 20px rgba(253, 253, 253, 0.082),
    0px 0px 0px 50px rgba(253, 253, 253, 0.082),
    0px 0px 0px 80px rgb(253, 253, 253, 0.082);
  z-index: -10;
}

@media (min-width: 768px) {
  .icon {
    height: 270px;
    width: 270px;
  }
  .icon.rock {
    border: 30px solid hsl(349, 71%, 52%);
    box-shadow: inset 0px 8px 8px grey;
    cursor: pointer;
  }
  .icon.paper {
    border: 30px solid hsl(230, 89%, 62%);
    box-shadow: inset 0px 8px 8px grey;
    cursor: pointer;
  }
  .icon.scissors {
    border: 30px solid hsl(39, 89%, 49%);
    box-shadow: inset 0px 8px 8px grey;
    cursor: pointer;
  }
  .icon.lizard {
    border: 30px solid hsl(261, 73%, 60%);
    box-shadow: inset 0px 8px 8px grey;
    cursor: pointer;
  }
  .icon.spock {
    border: 30px solid hsl(189, 59%, 53%);
    box-shadow: inset 0px 8px 8px grey;
    cursor: pointer;
  }
  .paper.winner,
  .scissors.winner,
  .rock.winner,
  .lizard.winner,
  .spock.winner {
    box-shadow: inset 0px 8px 8px grey, 0px 0px 0px 50px rgba(45, 51, 80, 0.7),
      0px 0px 0px 100px rgba(38, 46, 74, 0.7),
      0px 0px 0px 150px rgb(32, 39, 70, 0.7);
    z-index: -10;
  }
}


#toggleBtn {
  position: absolute;
  bottom: 3rem;
  left: 2rem;
  padding: 10px 10px;
  border: 2px solid hsl(217, 16%, 45%);
  border-radius: 0.4rem;
  color: hsl(217, 16%, 45%);
  background: none;
  font-weight: 600;
  letter-spacing: 2px;
  text-transform: uppercase;
}
#toggleBtn:hover {
  border: 3px solid white;
  background: hsl(230, 89%, 62%);
  color: white;
  cursor: pointer;
  transition: -webkit-transform 500ms ease-in-out;
  transition: transform 500ms ease-in-out;
  transition: transform 500ms ease-in-out, -webkit-transform 500ms ease-in-out;
}
